import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { SelectAjax, DatePickerString } from 'components/common';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Form, Input, Spin, message, Select } from 'antd';
import { createStaff, updateStaff, getStaffById, getStaffCategory } from 'services/teacher';
import { getFacultys } from 'services/faculty';
import { getSubjectGroups } from 'services/subjectGroup';
import { getQueryParams } from 'utils/urlUtil';

const { Option } = Select;

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    staffId: '',
    fullname: '',
    gender: '',
    facultyId: '',
    subjectGroupId: '',
    birthdate: '',
    titleId: '',
    positionId: '',
    rankId: '',
    degreeId: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTeacher' })
class FormTeacherPage extends Component {
  state = {
    staffId: true,
    initialValue: {},
    isSaving: false,
  };

  componentDidMount = () => {
    const { id: queryParamId } = getQueryParams();
    if (queryParamId) {
      this.setState({ staffId: queryParamId });
      this.getTeacherById(queryParamId);
    }
  };

  getTeacherById = async id => {
    const res = await getStaffById({ id });
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields(async (err, values) => {
      if (!err) {
        const { staffId } = this.state;
        try {
          this.setState({ isSaving: true });
          if (!staffId) {
            await createStaff(values);
          } else {
            await updateStaff({ staffId, ...values });
          }
          this.props.history.goBack();
        } catch (error) {
          message.error('C li xy ra');
        }
        this.setState({ isSaving: false });
      }
    });
  };

  render() {
    const { initialValue, isSaving } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { teacherId } = this.state;
    return (
      <Spin spinning={isSaving}>
        <Card title={teacherId ? 'Thm gio vin' : 'Cp nht gio vin'}>
          <Row>
            <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
              <Form.Item hasFeedback label="M CB" {...formItemLayout}>
                {getFieldDecorator('staffId', {
                  initialValue: initialValue.staffId,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lng nhp thng tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="H tn" hasFeedback>
                {getFieldDecorator('fullname', {
                  initialValue: initialValue.fullname,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lng nhp thng tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Khoa" hasFeedback>
                {getFieldDecorator('facultyId', {
                  initialValue: initialValue.facultyId,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lng nhp thng tin!',
                    },
                  ],
                })(<SelectAjax service={getFacultys} />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="B mn" hasFeedback>
                {getFieldDecorator('subjectGroupId', {
                  initialValue: initialValue.subjectGroupId,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lng nhp thng tin!',
                    },
                  ],
                })(<SelectAjax service={getSubjectGroups} />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Gii tnh" hasFeedback>
                {getFieldDecorator('gender', {
                  initialValue: initialValue.gender,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lng nhp thng tin!',
                    },
                  ],
                })(
                  <Select>
                    <Option value>Nam</Option>
                    <Option value={false}>N</Option>
                  </Select>
                )}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Ngy sinh" hasFeedback>
                {getFieldDecorator('birthdate', {
                  initialValue: initialValue.birthdate,
                })(<DatePickerString placeholder="T ngy" />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Chc danh">
                {getFieldDecorator('titleId', {
                  initialValue: initialValue.titleId,
                })(<SelectAjax service={async () => getStaffCategory({ categoryId: 0 })} />)}
              </Form.Item>

              <Form.Item {...formItemLayout} label="Chc v">
                {getFieldDecorator('positionId', {
                  initialValue: initialValue.positionId,
                })(<SelectAjax service={async () => getStaffCategory({ categoryId: 1 })} />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Hc v">
                {getFieldDecorator('degreeId', {
                  initialValue: initialValue.degreeId,
                })(<SelectAjax service={async () => getStaffCategory({ categoryId: 3 })} />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Hc hm">
                {getFieldDecorator('rankId', {
                  initialValue: initialValue.rankId,
                })(<SelectAjax service={async () => getStaffCategory({ categoryId: 2 })} />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label=" ">
                <BtnSave htmlType="submit" />
                <BtnCancel onClick={() => this.props.history.goBack()} />
              </Form.Item>
            </Form>
          </Row>
        </Card>
      </Spin>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormTeacherPage {...props} />
  </Page>
));



